/* General Rules */
* {
    box-sizing: border-box;
    transition: all 0.3s ease-in-out; }
.flex-container {
    display: flex;
    flex-wrap: wrap; }
/* Colors */
$blackWatto: #000;
$grayWatto: #2a2a2a;
$whiteWatto: #fff;
$lessWhiteWatto: #ddd;
$blueWatto: #3273C5;
$redWatto: #d63939;
$greenWatto: #0ec530;
/* Layout */
body {
    background-color: $blackWatto;
    background-image: url('../img/bg-watto.png');
    background-position: center;
    color: $whiteWatto;
    font-family: Arial; }
header {
    margin: 5em;
    a {
      text-decoration: none; } }
h1 {
    color: $whiteWatto;
    letter-spacing: 2px;
    text-align: center;
    text-transform: uppercase;
    &:hover {
        color: $blueWatto; } }
a {
    color: $whiteWatto; }
.content {
    margin: 0 auto;
    max-width: 1170px; }
.items {
    padding: 0;
    justify-content: space-between;
    .item {
        background: rgba($grayWatto, .8);
        border-bottom: 0.125rem solid $blueWatto;
        box-sizing: border-box;
        color: $lessWhiteWatto;
        list-style: none;
        margin-bottom: 5%;
        padding: 1em;
        width: 100%;
        a {
            color: $whiteWatto;
            text-decoration: none;
            &:hover {
                color: $blueWatto; } }
        img {
            width: 100%; } } }

/* Single */
.single-product {
    background: rgba($grayWatto, .8);
    flex-direction: column;
    padding: 2%;
    position: relative;
    h2 {
        border-bottom: 0.125rem solid $blueWatto;
        font-size: 1.8em;
        margin-bottom: 2em;
        padding: 1em;
        text-transform: uppercase;
        width: 100%; }
    .product-img {
        margin-bottom: 2em;
        width: 100%;
        img {
            width: 100%; }
        p {
            padding-left: 2%; } }
    .product-details {
        width: 100%;
        h3 {
            border-bottom: 0.125rem solid $blueWatto;
            padding-bottom: 1em;
            &:first-child {
                margin-top: 0; } }
        ul {
          list-style: none;
          padding: 0;
          li {
            display: flex;
            justify-content: space-between;
            line-height: 2em;
            span {
                display: block;
                text-align: right; } } }
        .price-container {
            border-top: 0.125rem solid $blueWatto;
            border-bottom: 0.125rem solid $blueWatto;
            p {
                font-size: 1.5em;
                color: $greenWatto;
                font-weight: bold;
                letter-spacing: 1px;
                text-align: center; }
            .out-of-stock {
                color: $redWatto;
                text-transform: uppercase; } } }
    .close {
        color: $blueWatto;
        position: absolute;
        right: 2%;
        font-size: 2em;
        &:hover {
            color: darken($blueWatto, 10%); } } }
.btn {
    background-color: $blueWatto;
    border-radius: 5px;
    display: block;
    font-weight: bold;
    margin: 2em auto;
    padding: 1.5em;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    max-width: 300px;
    width: 100%;
    &:hover {
        background-color: darken($blueWatto, 10%); }
    i {
        font-size: 1.2em;
        margin-right: 0.3em; } }



footer {
    margin: 5em; }

@media (min-width: 768px) {
  .single-product {
      flex-direction: row;
      .product-img {
          width: 40%;
          img {
            width: 100%; } }
      .product-details {
          padding: 0 0 0 2%;
          width: 60%; } } }

@media (min-width: 480px) {
    .items {
        .item {
            margin-bottom: 3%;
            width: 48.5%; } } }
